@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&family=Pacifico&display=swap);
@font-face {
  font-family: "SEGA LOGO FONT";
  font-style: normal;
  font-weight: normal;
  src: local("SEGA LOGO FONT"), url(/commentify_prod/static/media/SEGA.12a3db8b.woff) format("woff");
}

.container {
  display: block;
  overflow: hidden;
  width: 50%;
  height: 90vh;
  background-color: #fff;
  box-shadow: 0 0 5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin: 0 auto;
}

@media only screen and (max-width: 900px) {
  .container {
    width: 70%;
  }
}

@media only screen and (max-width: 700px) {
  .container {
    width: 90%;
  }
}

@media only screen and (max-width: 550px) {
  .container {
    width: 100%;
    height: 100vh;
    border-radius: 0;
  }
}

.header {
  text-align: center;
}

.header__text {
  margin: 0.5rem 0;
  background-image: linear-gradient(to right, #cf0d68, #dfd10f);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
  font-family: "Pacifico", cursive;
  font-size: 2.5rem;
  font-weight: 200;
  line-height: 2;
}

.header__text:hover {
  cursor: pointer;
}

/* SEARCHBOX */

.searchBox {
  margin: 1rem 0;
  padding: 0.5rem 0.5rem;
  background-color: #f1f1f1;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
}

.searchBox:focus {
  outline: none;
}

/* CARDS */

.card {
  display: flex;

  margin: 1rem;
  border-bottom: 1px solid #ddd;
}

.card:hover {
  background-color: #f1f1f1;
  cursor: pointer;
}
.card__img {
  display: block;
  height: 5rem;
  background-color: #3f88c5;
  margin: 1rem;
  border-radius: 50%;
}
.card__author {
  font-family: "Open-sans", sans-serif;
}

/* LOADING */

.loading {
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading__icon {
  font-size: 3rem;
  color: #ddd;
}

body {
  margin: 0;
  padding: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, #fde74c, #ef8354);
}

@media only screen and (max-width: 700px) {
  body {
    padding: 0;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 5px;
}

@media only screen and (max-width: 700px) {
  ::-webkit-scrollbar {
    width: 3px;
  }
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #000;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

